import { UpdateElemets } from "../UpdateElements";
import { ClearData } from "./Datadealer";

export function HardDataReset(){

    // safty check
    if (window.confirm("Mchtest du wirklich alle Daten lschen(unwiederrufbar)?")){
        //clear data
        ClearData()
        UpdateElemets()
        // Reload Website
        window.location.reload();
    }
}